
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import value2name from 'vux/src/filters/value2name.js'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


import moment from 'moment';
import { setTimeout } from 'timers';

export default {
directives: {
TransferDom
},
components: {
PopupPicker,
Group,
XAddress,
ChinaAddressV4Data,
XButton,
Box,
XTextarea,
Alert,
Toast
},
data() {
return {
type: '',

title1: "",
list1: [["", ""]],

placeholder1: "",

title2: "",
list2: [["", ""]],

placeholder2: "",

title3: "",  // 

addressData: ChinaAddressV4Data,

title4: "",
placeholder4:
"",

title5: "",
placeholder5: "",

show: false,
title6: "",
summary: "",

show_success: false,


addForm: {
gender: [""],
want_gender: [""],
}


};
},
created() {










if (this.$route.params.type == "wish") {
this.type = 'wish';
this.tabbar_image = 'http://cdn.collhome.com/open/vux/images/wish_bar.jpeg';
} else if (this.$route.params.type == "goodnight") {
this.type = 'goodnight';
this.tabbar_image = 'http://cdn.collhome.com/open/vux/images/goodnight_bar.jpeg';
this.title4 = "";
this.placeholder4 = "";
} else {
this.type = 'trouble';
this.tabbar_image = 'http://cdn.collhome.com/open/vux/images/trouble_bar.jpg';
this.title2 = "";
this.title4 = "";
this.placeholder4 = "";
}
},
methods: {
getName(value) {
return value2name(value, ChinaAddressV4Data);
},
submit() {
let that = this;
console.log('this.addForm', this.addForm);
if (this.addForm.wish && this.addForm.wechat) {
let address;
if (this.addForm.address) {
address = this.getName(this.addForm.address);
} else {
address = '';
}
let postData = {
type: this.type,
gender: this.addForm.gender[0],
want_gender: this.addForm.want_gender[0],
address: address,
wish: this.addForm.wish,
wechat: this.addForm.wechat,

}
console.log('postData', postData);
this.$http.post('wishs', postData).then((res) => {
if (res.status < 300) {
this.show_success = true;
setTimeout(function(){
that.goBack();
}, 1000)
}
console.log('post wishs res', res);
})
} else {
this.show = true;
}
},

goBack () {
window.history.length > 1
? this.$router.go(-1)
: this.$router.push('/')
}
}
};



