
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'


export default {
components: {
PopupPicker,
Group,
XButton,
Box,
},
data() {
return {
title1: "",
list1: [["", ""]],
value1: [''],
placeholder1: "",
};
},
created() {
if (this.$route.params.type == "wish") {
this.tabbar_image = 'http://cdn.collhome.com/open/vux/images/wish_bar.jpeg';
} else if (this.$route.params.type == "goodnight") {
this.tabbar_image = 'http://cdn.collhome.com/open/vux/images/goodnight_bar.jpeg';
} else {
this.tabbar_image = 'http://cdn.collhome.com/open/vux/images/trouble_bar.jpg';
}
},
methods: {
submit() {
this.$router.push('/wish/show?page=1');
}
}
};



