
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

export default {
components: {
XButton,
Box,
Flexbox,
FlexboxItem
},
created() {
if (this.$route.params.type == "wish") {
this.title1 = '';
this.title2 = '';
this.link1 = '/wish/create';
this.link2 = '/wish/accept';
this.tabbar_image = 'http://cdn.collhome.com/open/vux/images/wish.jpeg';
} else if (this.$route.params.type == "goodnight") {
this.title1 = '';
this.title2 = '';
this.link1 = '/goodnight/create';
this.link2 = '/goodnight/accept';
this.tabbar_image = 'http://cdn.collhome.com/open/vux/images/goodnight.jpeg';
} else {
this.title1 = '';
this.title2 = '';
this.link1 = '/trouble/create';
this.link2 = '/trouble/accept';
this.tabbar_image = 'http://cdn.collhome.com/open/vux/images/trouble.jpeg';
}
}
};



