import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import HelloFromVux from '@/components/HelloFromVux'
import XInput from '@/components/XInput'
import XTextarea from '@/components/XTextarea'
import WishType from '@/components/WishType'
import WishCreate from '@/components/WishCreate'
import WishAccept from '@/components/WishAccept'
import WishShow from '@/components/WishShow'
import Test from '@/components/Test'

Vue.use(Router)

const routes = [
  {
    path: '/',
    component: HelloFromVux
  },
  {
    path: '/helloWorld',
    component: HelloWorld
  },
  {
    path: '/xInput',
    component: XInput
  },
  {
    path: '/xTextarea',
    component: XTextarea
  },
  {
    path: '/:type/type',
    component: WishType
  },
  {
    path: '/:type/create',
    component: WishCreate
  },
  {
    path: '/:type/accept',
    component: WishAccept
  },
  {
    path: '/:type/show',
    component: WishShow
  },
  {
    path: '/test',
    component: Test
  }
]

export default new Router({
  // routes: [
  //   {
  //     path: '/',
  //     name: 'HelloWorld',
  //     component: HelloWorld
  //   }
  // ]

  routes
})



